import { forwardRef, useEffect } from "react";
import { ParallaxBanner } from "react-scroll-parallax";
import { animated as a, useSpring } from "react-spring";

import Col from "../../components/Col";
import Row from "../../components/Row";

import NavBar from "../../components/NavBar";
import { NavItem } from "../../types";

type Props = {
  children?: React.ReactNode;
  navItems?: Array<NavItem>;
  isVisible?: boolean;
  onRendered?: () => void;
};

const CoupleParallax = forwardRef(
  (
    { navItems, isVisible, onRendered }: Props,
    ref: React.Ref<HTMLDivElement>
  ) => {
    const textAnimation = useSpring({
      config: { mass: 80, tension: 800, friction: 500 },
      opacity: isVisible ? 1 : 0,
      y: isVisible ? 0 : 40,
      from: { opacity: 0, y: 40 },
    });
    const akadDate = "2022-10-08T01:00:00.000Z";

    const image = `${process.env.PUBLIC_URL}/aqiqah.jpg`;

    useEffect(() => {
      if (onRendered) {
        onRendered();
      }
    }, [onRendered]);

    return (
      <ParallaxBanner
        layers={[{ image, translateY: [20, -10] }]}
        style={{
          height: "100vh",
        }}
      >
        <div
          style={{
            backgroundColor: "rgba(0,0,0,0.5)",
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            position: "absolute",
            color: "white",
          }}
          ref={ref}
        >
          <Row
            align="center"
            verticalAlign="verticalCenter"
            mobileHorizontal
            pageSection
          >
            <Col size={10}>
              <a.h4 style={textAnimation} className="text-center text-white">
                <span>
                  We are just having a <strong>baby boy</strong>!
                </span>
              </a.h4>
              {navItems && isVisible && (
                <NavBar
                  direction="row"
                  items={navItems}
                  position="relative"
                  animationDelay={500}
                ></NavBar>
              )}
            </Col>
          </Row>
        </div>
      </ParallaxBanner>
    );
  }
);

export default CoupleParallax;
