import React from "react";
import cx from "classnames";
import styles from "./style.module.css";

type Props = {
  align: "left" | "right" | "center";
  pageSection?: boolean;
  reverseOnMobile?: boolean;
  mobileHorizontal?: boolean;
  verticalAlign?: "verticalCenter" | "bottom" | "top";
  noMargin?: boolean;
  children: React.ReactNode;
};

const Row: React.FC<Props> = ({
  children,
  align,
  pageSection,
  reverseOnMobile,
  mobileHorizontal,
  noMargin,
  verticalAlign,
}) => {
  const defaultVerticalAlign = "center";
  const selectedVerticalAlign = verticalAlign || defaultVerticalAlign;
  return (
    <div
      className={cx(styles.row, styles[align], styles[selectedVerticalAlign], {
        [styles.pageSection]: pageSection,
        [styles.reverseOnMobile]: reverseOnMobile,
        [styles.mobileHorizontal]: mobileHorizontal,
        [styles.noMargin]: noMargin,
      })}
    >
      {children}
    </div>
  );
};

export default Row;
