import React from "react";
import cx from "classnames";

import styles from "./style.module.css";

type Props = {
  size: number;
  mobile?: number;
  children: React.ReactNode;
  desktopOnly?: boolean;
  mobileOnly?: boolean;
  noMargin?: boolean;
  stretch?: boolean;
};

const Col: React.FC<Props> = ({
  children,
  size,
  mobile,
  desktopOnly,
  mobileOnly,
  noMargin,
  stretch,
}) => {
  return (
    <div
      className={cx(
        styles.col,
        styles[`col-${size}`],
        {
          [styles[`mobile-${mobile}`]]: mobile,
        },
        { [styles.desktopOnly]: desktopOnly },
        { [styles.mobileOnly]: mobileOnly },
        { [styles.noMargin]: noMargin },
        { [styles.stretch]: stretch }
      )}
    >
      {children}
    </div>
  );
};

export default Col;
